.outerContainer{
    width: 100%;
    min-height: 100vh;
    background-color: rgb(0, 0, 0 ,.4);
    position: fixed;
    top: 0;
    left: 0;
    z-index: 600000;
    display: flex;
    align-items: center;
    place-content: center;
}
.innerContainer{
width: 95%;
height: 90vh;
background-color: white;
border-radius: 20px;
display: flex;
align-items: center;
justify-content: center;
flex-direction: column;
padding: 1rem;
}
.PaymentSessionIdContainer{
    display: flex;
    flex-direction: column;
    align-items: center;
    place-content: center;
}
.container_SubHeading{
    font-weight: 400;
}
.chooseComponentsText{
    font-weight: 400;
    margin-top: 1rem;
    margin-bottom: 1.5rem;
}
.componentsContainer{
    display: flex;
    flex-wrap: wrap;
}
.individualComponent{
    display: flex;
    align-items: center;
    margin-right: 1rem;
    margin-bottom: 1rem;
}
.inputLabel{
    margin-left: .25rem;
}
.makePaymentContainer{
    width: 80%;
    height: 80%;
    display: flex;
    flex-direction: column;
    padding: 1rem;
    position: relative;
}
.cross {
    position: absolute;
    top: 1rem;
    right: 1rem;
    font-size: 1.5rem;
    cursor: pointer;
}
.cross > img {
    width: 2rem;
    height: 2rem;
    cursor: pointer;
}
.makePaymentContainerHeading{
    text-align: center;
    margin-bottom: 1rem;
    font-size: 2rem;
}
.makePaymentContainerSubText{
    font-size: 1.1rem;
    margin-bottom: 1rem;
    /* text-transform: capitalize; */
    width: 40%;
    margin: auto;
    margin-bottom: 1.5rem;
}
.makePaymentContainerAmount{
    font-weight: bold;
}
.makePaymentContainerPayButton{
    border-radius: 6px;
    /* background-color: rgb(105, 51, 211); */
    background-color: #2A72DE;
    color: rgb(255, 255, 255);
    border: none;
    font-size: 14px;
    padding: 11px 16px;
    margin-bottom: 1.5rem;
    margin-top: 1.5rem;
    cursor: pointer;
    width: 300px;
    margin: auto;
}


@media(max-width:1200px){
    .makePaymentContainerSubText{
        width: 90%;
        
    }
}